	width: 100%;
	margin-top: 20rpx;
	.list-item{
		width: 100%;
		height: auto !important;
		height: 220rpx;
		background-color: #fff;
		font-size: 26rpx;
		margin-bottom: 20rpx;
		.t {
			height: 80rpx;
			line-height: 80rpx;
			border-bottom: 2rpx solid #ebebeb;
			text { font-size: 32rpx; color: #333; }
			text:first-child {
				padding-left: 32rpx; float: left;
			}
			text:last-child {
				padding-right: 32rpx; float: right;
			}
		}
		.box {
			height: 136rpx;
			width: 100%;
			.item-box {
				width: 100%;
				height: 32rpx;
				float: left;
				font-size: 28rpx; color: #888;
				padding-top: 20rpx;
				view {
					text { padding-right: 10rpx; position: relative; top: 2rpx; }
				}
				view:nth-child(1) { padding-left: 32rpx; float: left; }
				view:nth-child(2) { padding-right: 32rpx; float: right; }
				.c-1 { color: #888; }
				.c-3 { color: #b91922; }
				.c-2 { color: green; }

				&:nth-child(3) {
					padding: 20rpx 32rpx;
				}
			}
		}
	}

}
</style>
